@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* App.css */

:root {
  --nude: #FFE0C3;
  --bright: #ffffff;
  --dark: #000000;
  --typography: #000000;
  --map-land-fill:#FFE0C3; /* ⚠️ no space between ":" and "#" */
  --map-land-stroke:none; /* ⚠️ can be 'none' or '#123456' */
  font-size: 12px;
}

html { font-family: 'Inter', sans-serif; }

body { color: #000000; color: var(--typography); }

.root {
  width: 100%;
  height: 100%;
  background-color: #FFE0C3;
  background-color: var(--nude);
}

/* Mobile (max-width 767px) */
h1 {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 48px;
  margin: 0;
}

h2 {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 14px;
  margin: 0;
}

h3 {
  font-family: 'Victor Mono', monospace;
  font-weight: 400;
  font-style: italic;
  font-size: 24px;
  margin: 0;
}

h4 {
  font-family: 'Victor Mono', monospace;
  font-weight: 600;
  font-style: italic;
  font-size: 16px;
  margin: 0;
}

h5 {
  font-family: 'Victor Mono', monospace;
  font-weight: 400;
  font-style: italic;
  font-size: 14px;
  margin: 0;
}

h6 {
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  font-size: 12px;
  text-transform: uppercase;
  margin: 0;
}

.App {
  min-height: 100vh;
  width: 100vw;
  display: grid;
  /*
  The grid template of .App is re-defined in the .css of every singular components
  in case the layout has to change (media queries included).
  */
}

.header {
  background-color: #ffffff;
  background-color: var(--bright);
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 25% 50% 25%;
  align-items: center;
}
.header > * {
  padding: 0 20px;
}

.selector {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border-top: 1px solid #000000;
  border-top: 1px solid var(--dark);
}

.selector button {
  background-color: #ffffff;
  background-color: var(--bright);
  border: none;
}
.selector button:not(:last-of-type) {
  border-right: 1px solid #000000;
  border-right: 1px solid var(--dark);
}
.selector button.active {
  background-color: transparent;
}

button:focus {
  outline: none;
}

.blend-multiply {
  mix-blend-mode: multiply;
}

/* tablet portrait */
@media all and (min-width: 768px) {
  h1 {font-size: 96px;}
  h2 {}
  h3 {font-size: 28px;}
  h4 {font-size: 21px;}
  h5 {font-size: 16px;}
  h6 {font-size: 16px;}
}

/* tablet landscape and up */
@media all and (min-width: 1024px) {
  h1 {font-size: 84px;}
  h2 {}
  h3 {font-size: 28px;}
  h4 {font-size: 21px;}
  h5 {font-size: 16px;}
  h6 {font-size: 16px;}
}
/* Root.css */
.App {
    background-color: var(--nude);
    grid-template-columns: 100%;
    grid-template-rows: 4rem 3rem auto 7rem 7rem 7rem;
}

.title-box {
    border-top: 1px solid var(--dark);
    display: flex;
    align-items: center;
    padding: 20px;
  }

.country {
    background-color: var(--bright);
    border-top: 1px solid var(--dark);
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
}
.country.active {
    background-color: transparent;
}

/* tablet portrait */
@media all and (min-width: 768px) {
    .App {
        background-color: var(--nude);
        grid-template-columns: 100%;
        grid-template-rows: 6rem 4rem auto 10rem 10rem 10rem;
    }
}
  
/* tablet landscape and up */
@media all and (min-width: 1024px) {
    .App {
        background-color: var(--nude);
        grid-template-columns: 50% 50%;
        grid-template-rows: 6rem 5rem repeat(3, auto);
    }
    .title-box {
        grid-column: 1 / span 1;
        grid-row: 1 / -1;
        border-top: none;
        padding: 30px;
    }
}
/* Visualization.css */

.App.visualization-component {
    background-color: var(--bright);
}

.visualization-container {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 4rem 3rem auto 3em;

    height: 100%;
    width: 100%;
}

#visualization {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
    overflow: hidden;
}

.visualization-component .header {
    grid-column: 1 / -1;
    grid-row: 1 / span 1;
    z-index: 10000;
}

.selector-date {
    grid-column: 1 / -1;
    grid-row: 2 / span 1;
    z-index: 10000;
    display: grid;
    grid-template-columns: 5rem auto 5rem;
    border-top:  1px solid var(--dark);
    border-bottom:  1px solid var(--dark);
}
.selector-layout {
    z-index: 10000;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border-top:  1px solid var(--dark);
}
.selector-layout button:not(:last-of-type) {
    border-right: 1px solid var(--dark);
}
.selected-date {
    background-color: var(--nude);
    display: flex;
    align-items: center;
    justify-content: center;
}
.selector-date > * {
    grid-row: 1 / span 1;
    overflow: hidden;
}
.selector-date > *:not(:last-child) {
    border-right: 1px solid var(--dark);
}
.selector-date h3 {
    font-style: normal;
    text-align: center;
}
.selector-layout {
    grid-column: 1 / -1;
    grid-row: -2 / span 1;
    z-index: 10000;
}
.visualization-component button {
    background-color: transparent;
}

.visualization-component button.active {
    background-color: var(--nude);
}

/* tablet portrait */
@media all and (min-width: 768px) {
    .visualization-container {
        grid-template-rows: 4rem 4rem auto 4em;
    }
    .selector-date {
        grid-template-columns: 10rem auto 10rem;
    }
  }
  
  /* tablet landscape and up */
  @media all and (min-width: 1024px) {
    .visualization-container {
        grid-template-rows: 4rem 5rem auto 5em;
    }
    .selector-date {
        grid-template-columns: 14rem auto 14rem;
    }
  }
